<template>
  <div class="Helmet">
    <div class="three-canvas"></div>
  </div>
</template>

<script type="module">
import * as THREE from 'three'
import {RGBELoader} from 'three/examples/jsm/loaders/RGBELoader'
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader'
import {EquirectangularToCubeGenerator} from 'three/examples/jsm/loaders/EquirectangularToCubeGenerator'
import {PMREMGenerator} from 'three/examples/jsm/pmrem/PMREMGenerator'
import {PMREMCubeUVPacker} from 'three/examples/jsm/pmrem/PMREMCubeUVPacker'
import mixin from './common/common'

import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'
const clock = new THREE.Clock()
export default {
  name: 'Helmet',
  mixins: [mixin],
  methods: {
    getRender ({width, height, dom}) {
      const renderer = new THREE.WebGLRenderer()
      renderer.setSize(width, height)
      dom.appendChild(renderer.domElement)
      return renderer
    },
    getCamera () {
      const camera = new THREE.PerspectiveCamera(75, this.width / this.height, 1, 10000)
      camera.position.set(0, 0, 2.5)
      camera.lookAt(0, 0, 0)
      return camera
    },
    loadSourse () {
      new RGBELoader()
        .setDataType(THREE.UnsignedByteType)
        .setPath('../../static/textures/equirectangular/')
        .load('pedestrian_overpass_2k.hdr', (texture) => {
          console.log(texture)
          const cubeGenerator = new EquirectangularToCubeGenerator(texture, { resolution: 1024 })
          cubeGenerator.update(this.renderer)
          const pmremGenerator = new PMREMGenerator(cubeGenerator.renderTarget.texture)
          pmremGenerator.update(this.renderer)
          const pmremCubeUVPacker = new PMREMCubeUVPacker(pmremGenerator.cubeLods)
          pmremCubeUVPacker.update(this.renderer)
          const envMap = pmremCubeUVPacker.CubeUVRenderTarget.texture
          // model
          const loader = new GLTFLoader().setPath('../../static/models/gltf/DamagedHelmet/glTF/')
          loader.load('DamagedHelmet.gltf', (gltf) => {
            gltf.scene.traverse(function (child) {
              if (child.isMesh) {
                child.material.envMap = envMap
              }
            })
            this.scene.add(gltf.scene)
          })
          pmremGenerator.dispose()
          pmremCubeUVPacker.dispose()
          this.scene.background = cubeGenerator.renderTarget
        })
    },
    init () {
      // render cube
      // console.log(this.comWidth)
      this.scene = new THREE.Scene()
      this.scene.background = new THREE.Color(0xaaccff)
      // render camera
      this.camera = this.getCamera()
      this.renderer = this.getRender({
        width: this.width,
        height: this.height,
        dom: document.querySelector('.three-canvas')
      })
      this.renderer.gammaOutput = true
      // 加载资源
      this.loadSourse()
      this.controls = new OrbitControls(this.camera)

      this.renderer.render(this.scene, this.camera)
    },
    animate () {
      requestAnimationFrame(this.animate)
      const delta = clock.getDelta()
      // 渲染模型将场景,在相机视角渲染
      this.controls.update(delta)
      this.renderer.render(this.scene, this.camera)
    }
  },
  mounted () {
    this.init()
    window.addEventListener('resize', this.resize)
    this.animate()
  }
}
</script>

<style scoped>
.Helmet{

}
</style>



