<template>
  <div id="list" :style="'width:'+comWidth+'px;'">
    <ul>
      <li v-for='(item,index) in list' :key="index"><router-link :to="item.path">{{item.name}}</router-link></li>
    </ul>
  </div>
</template>

<script>
import router from '../router/index'
export default {
  name: 'list',
  props: ['comWidth'],
  data () {
    return {
      list: router.options.routes
    }
  }
}
</script>

<style scoped>
  #list{
    flex-shrink: 0;
    /*width: 300px;*/
    height: 100vh;
    background: #efefef;
  }
#list a{
  color: red;
}
</style>



