<template>
  <div class="three-canvas"></div>
</template>

<script>

import * as THREE from 'three'
import {PointerLockControls} from 'three/examples/jsm/controls/PointerLockControls'
import mixin from './common/common'
export default {
  name: 'FirstSight',
  mixins: [mixin],
  methods: {
    getCamera () {
      const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 1000)
      camera.position.z = 30
      return camera
    },
    getCube (w1 = 10, w2 = 10, w3 = 10) {
      this.geometry = new THREE.BoxGeometry(w1, w2, w3)
      this.material = new THREE.MeshNormalMaterial()
      return new THREE.Mesh(this.geometry, this.material)
    },
    getLight () {
      return THREE.Light()
    },
    init () {
      // render cube
      this.cube = this.getCube()
      this.scene = new THREE.Scene()
      const light = new THREE.HemisphereLight(0xeeeeff, 0x777788, 0.75)
      light.position.set(0.5, 1, 0.75)
      this.scene.add(light)
      this.controls = new PointerLockControls(this.camera)
      this.controls.lock()
      // render camera
      this.camera = this.getCamera()
      this.renderer = this.getRender('.three-canvas')
      this.scene.add(this.cube)
      this.light = this.getLight()
      this.scene.add(this.light)
      this.renderer.render(this.scene, this.camera)
    },
    animate () {
      requestAnimationFrame(this.animate)
      this.cube.rotation.x += 0.01
      this.cube.rotation.y += 0.01
      // 渲染模型将场景,在相机视角渲染
      // this.controls.update()
      this.renderer.render(this.scene, this.camera)
    }
  },
  mounted () {
    this.init()
    window.addEventListener('resize', this.resize)
    this.animate()
  }
}
</script>

<style scoped>
  .three-canvas{
    width: 100%;
  }
  canvas { width: 100%; height: 100% }
</style>



