<template>
  <div class="three-canvas"></div>
</template>

<script>
import * as THREE from 'three'
import {FlyControls} from 'three/examples/jsm/controls/FlyControls'
import mixin from './common/common'
const clock = new THREE.Clock()
export default {
  name: 'MoveCube',
  mixins: [mixin],
  methods: {
    getCube (w1 = 10, w2 = 10, w3 = 10) {
      this.geometry = new THREE.BoxGeometry(w1, w2, w3)
      this.material = new THREE.MeshNormalMaterial()
      return new THREE.Mesh(this.geometry, this.material)
    },
    getCamera () {
      const camera = new THREE.PerspectiveCamera(75, this.width / this.height, 1, 10000)
      camera.position.set(0, 0, 300)
      camera.lookAt(0, 0, 0)
      return camera
    },
    createFlyControls (camera) {
      const controls = new FlyControls(camera)
      controls.movementSpeed = 100
      controls.rollSpeed = Math.PI / 6
      controls.autoForward = false
      controls.dragToLook = true
      return controls
    },
    init () {
      // render cube
      // console.log(this.comWidth)
      this.scene = new THREE.Scene()
      this.scene.background = new THREE.Color(0xaaccff)
      // render camera
      this.camera = this.getCamera()
      this.renderer = this.getRender('.three-canvas')

      this.group = new THREE.Group()
      for (let i = 0; i < 1000; i++) {
        const cube = this.getCube()
        cube.position.x = Math.random() * 500 - 250
        cube.position.y = Math.random() * 500 - 250
        cube.position.z = Math.random() * 500 - 250
        cube.rotation.x = Math.random() * 2 * Math.PI
        cube.rotation.y = Math.random() * 2 * Math.PI
        this.group.add(cube)
      }
      this.scene.add(this.group)

      this.controls = this.createFlyControls(this.camera)

      this.renderer.render(this.scene, this.camera)
    },
    animate () {
      requestAnimationFrame(this.animate)
      const delta = clock.getDelta()
      // 渲染模型将场景,在相机视角渲染
      this.controls.update(delta)
      this.renderer.render(this.scene, this.camera)
    }
  },
  mounted () {
    this.init()
    window.addEventListener('resize', this.resize)
    this.animate()
  }
}
</script>

<style scoped>

</style>



